import React, { Component } from 'react';
import {Route} from 'react-router-dom';
import axios from 'axios'
import Callback from './Callback';
import { Redirect } from 'react-router-dom'
import {
    BrowserRouter as Router,
    Switch,
    Link
  } from "react-router-dom";

import Cookies from 'universal-cookie';
import Search from 'react-search'
import './Ranking.css';
import {refresh_token} from './Api'
class Ranking extends React.Component {
    constructor(props)
    {
    super(props);
    const list=[];
    this.state = {
        object:[],
        problemwiseresult:[],
        clicked:"",
        institution:"",
        problemscore:[],
        problemcodes:[],
        problemlist:[],

    
    }
    this.ranking=this.ranking.bind(this);
     
    
    }
    getScore=(pass1)=>{
      var marks=[],p,q;
        for(p=0;p<this.state.problemcodes.length;p++){
            for(q=0;q<pass1.problemScore.length;q++){
                if(pass1.problemScore[q].problemCode==this.state.problemcodes[p]){
                  marks.push(pass1.problemScore[q].score);
                  break;
                }
             }
             if(q==pass1.problemScore.length) marks.push(0);
        }
        return marks.map((pass2,p2)=> <td key={p2}>{pass2}</td>)
  }
   
    ranking = () =>
    {

        const cookies = new Cookies();
        var p=0;
        var token1=cookies.get('username');
        var value1=cookies.get('contestcode');
        var value=cookies.get('problemcode');
        let object=null;
        axios.get(`https://api.codechef.com/rankings/${value1}?fields=&country=&institution=&institutionType=&offset=&limit=&sortBy=&sortOrder=`,
        { headers: {'Content-Type': 'application/json',
              'Accept': 'application/json',
            'Authorization': 'Bearer '+ token1 } }
           ) .then(res => {
            var pcodes=(res.data.result.data.content["0"].problemScore.map((item)=>item.problemCode.toString()));   
            this.setState({object:res.data.result.data.content,problemwiseresult:pcodes})})
      .catch(function (error)
      {
        console.log(error.message);
      });
      const array=this.state.object;
     
        console.log(this.state.object);
        
    }
    contestdetails=()=>
    {
      const cookies = new Cookies();

  var token1=cookies.get('username');
  var value=cookies.get('contestcode');

      axios.get(`https://api.codechef.com/contests/${value}`,
      { headers: {'Content-Type': 'application/json',
            'Accept': 'application/json',
          'Authorization': 'Bearer '+ token1 } }
    ) .then(res => {  
      this.setState( {problemlist:res.data.result.data.content.problemsList})})
         .catch(function (error)
    {
      console.log(error.message);
    });
    console.log(this.state.problemlist)
      if(this.state.problemlist.length>0)
      {
        console.log(this.state.problemlist.length);
        console.log(this.state.problemlist)
        var pCode=this.state.problemlist.map((item)=>item.problemCode)
          
            this.setState({problemcodes:pCode});
            console.log(this.state.problemcodes.length);
            this.setState({clicked:"Yes"});
           
      }

    }
    logout = (e)=>
    {
     
      console.log(this.state.clicked)
      const cookies=new Cookies();
      cookies.remove('username');
      cookies.remove('refresh_token');
      this.setState({clicked:"logout"})
      
    }
      render(){
        refresh_token();
        if(this.state.clicked=="logout")
        {
          return <Redirect to="/frontend/" />
        }
        if(this.state.clicked=="Yes")
        {
           
          return (
            <div>
            
            <div className="container">
            <button onClick={this.logout} className="butoon3">Logout</button>
               <div className="respTable">
              <table className="p">
                    <tbody>
                        <tr className="pppp">
                            <th className="hh"><b>RANK</b></th>
                            <th className="hh"><b>USERNAME</b></th>
                            <th className="hh"><b>TOTAL SCORE</b></th>
                            {this.state.problemcodes.map((item,i)=> <th className="hh" key={i}>{item}</th>)}
                        </tr>
                       
                        {this.state.object.map((item,i)=>
                            <tr className="pppp" key={i}>
                                <td >{item.rank.toString()}</td>
                                <td>{item.username}</td>
                                <td>{item.totalScore}</td>
                                {this.getScore(item)}
                            </tr>
                           
                        )}
                    </tbody>
                    
                </table>
                 
            </div>
            </div>







           
            </div>


          )
        }
    
        
        return (
          <div>
             {this.ranking()}
           {this.contestdetails()}
          </div>
        );
      }
    }
    export default Ranking;
    