import React, { Component, useCallback } from 'react';
import {Route} from 'react-router-dom';
import axios from 'axios'
import Callback from './Callback';
import { Redirect } from 'react-router-dom'
import {
    BrowserRouter as Router,
    Switch,
    Link
  } from "react-router-dom";

import Cookies from 'universal-cookie';
import Search from 'react-search'
import './Problem.css'
import Timer from 'react-compound-timer';
import ReactHtmlParser from 'react-html-parser'; 
import {refresh_token} from './Api'
class Problem extends React.Component
{
  constructor(props)
{
super(props);
const cookies = new Cookies();
const list=[],codelist=[],finallist=[],problemlist=[];
this.state = {
 list,
 codelist,
 problemlist,
 finallist,
token:"ht",
taxt:'',
object:null,
clicked:"A",
suggestions:[],
show:false,
problemname:'',
var:'A',
starttime:0,
lasttime:0,
currenttime:0 ,
starttime1:0,
lasttime1:0,
currenttime1:0 ,
bannerfile:'',
solved:null,
ram:[],
solved1:null,
username1:'',
length:20,


}
this.showproblem=this.showproblem.bind(this);
this.showranking=this.showranking.bind(this);

}
sleep=(miliseconds) =>{
  var currentTime = new Date().getTime();

  while (currentTime + miliseconds >= new Date().getTime()) {
  }
}
getinfo=(pass1)=>{
  const cookies = new Cookies();
  var value=cookies.get('contestcode').toString();
  var token1=cookies.get('username');
  var username1=this.state.username1;
  console.log(username1)
  var salve=null;
  console.log(value);
  axios.get(`https://api.codechef.com/submissions/?result=&year=&username=${username1}&language=&problemCode=&contestCode=${value}&fields=`,
      { headers: {'Content-Type': 'application/json',
            'Accept': 'application/json',
          'Authorization': 'Bearer '+ token1 } }
      ) .then(res => {this.setState({object:res.data.result.data.content})})
      .catch(function (error)
      {
        console.log(error.message);
      });
      if(this.state.object!=null)
      {
       var langht=20;
       if(this.state.object.length<langht)
        {this.setState({
          length:this.state.object.length
        })}
      }

}



showproblem= (e) => {
 
    const cookies = new Cookies();

  var token1=cookies.get('username');
  var value=cookies.get('contestcode');

  axios.get(`https://api.codechef.com/contests/${value}?fields=&sortBy=&sortOrder="asc"`,
  { headers: {'Content-Type': 'application/json',
        'Accept': 'application/json',
      'Authorization': 'Bearer '+ token1 } }
) .then(res => {console.log(res);   
  this.setState( {problemlist:res.data.result.data.content.problemsList,starttime:res.data.result.data.content.startDate,lasttime:res.data.result.data.content.endDate,bannerfile:res.data.result.data.content.bannerFile})})
.catch(function (error)
{
  console.log(error.message);
});
  console.log(this.state.problemlist.length);



  }


  
  converttomili=()=>
{
var starttime=this.state.starttime.toString();
var date = new Date(starttime); // some mock date
var milliseconds = date.getTime();
var endtime=this.state.lasttime.toString();
var date1 = new Date(endtime); // some mock date
var milliseconds1 = date1.getTime();
console.log(milliseconds);
console.log(milliseconds1);
var today = Math.round((new Date()).getTime() );
console.log(today)
this.setState({starttime1:milliseconds,lasttime1:milliseconds1,currenttime1:today});
}

  showranking = () => {
    this.setState(() => ({clicked:"Ranking"}));
     }
     logout = (e)=>
     {
      
       console.log(this.state.clicked)
       const cookies=new Cookies();
       cookies.remove('username');
       cookies.remove('refresh_token');
       this.setState({clicked:"logout"})
       
     }
  
  userinfo = ()=>
  {
    const cookies = new Cookies();
    var value=cookies.get('contestcode').toString();
    var token1=cookies.get('username');
    var salve=null;
    console.log(value);
    axios.get(`https://api.codechef.com/users/me`,
        { headers: {'Content-Type': 'application/json',
              'Accept': 'application/json',
            'Authorization': 'Bearer '+ token1 } }
        ) .then(res => { salve=res.data.result.data.content.problemStats.solved;
        this.setState({username1:res.data.result.data.content.username,solved:salve})})
        .catch(function (error)
        {
          console.log(error.message);
        });
        console.log(this.state.username1);

  }

       
  
 
     render()
  {
   
 
    const cookies = new Cookies();

    refresh_token();
    var value=cookies.get('contestcode');
    var value1="FEB19B";
  let problemlisto=(this.state.problemlist.map((item)=>item.problemCode.toString()));
  const url='/'+{value}+'/problem/'
  if(this.state.clicked=="AA")
  {
      var code='/frontend/contest/problem/'+this.state.taxt.toString();

    console.log(this.state.taxt);
    return <Redirect to={'/frontend/contest/problem/'+this.state.taxt.toString()}/>
  }
  if(this.state.clicked=="Ranking")
  {
     
    return <Redirect to={'/frontend/contest/ranking/'}/>
  }
  if(this.state.clicked=="logout")
  {
    return <Redirect to="/frontend/" />
  }

    return (
      <div>
         <button onClick={this.logout} class="butoon3">Logout</button>
      <div className="allpage">
       
       {this.state.problemlist.length===0 && this.showproblem()}
       <div className="first">
      
        <img src={this.state.bannerfile} alt="ram" className="image"/>
        
       
               <table className="bb">
                    <tbody>
                        <tr className="b">
                            <th className="b"><b>Problem name</b></th>
                            <th className="b"><b>successful submission</b></th>
                            <th className="b"><b>accuracy</b></th>
                        </tr>
                        {this.state.problemlist.map((item,i)=>
                            <tr key={i} className="b">
                                <td className="b"><Link to={'/contest/problem/'+item.problemCode}>{item.problemCode}</Link> </td>
                                <td className="b">{item.successfulSubmissions}</td>
                                <td className="b">{item.accuracy.toFixed(2)}</td>
                        
                            </tr>
                        )}
                    </tbody>
               </table>
        
            </div>
            <div className="second">
            
                {this.state.currenttime1===0 && this.converttomili()}
               <div className="ranking"> 
                <div className="ranklist"><p className="a"><u>Contest Ranklist</u></p>
               <button onClick={this.showranking} className="butoon4">Go to Contest Rank</button>
               
              
               </div></div>
               <div className="ranking1">
                 <div className="ranklist1">
                {
                  
                  
                    Number(this.state.currenttime1>0) && ((Number(this.state.lasttime1)<Number(this.state.currenttime1))?<p className="a"><u>Contest Ends</u></p>:<p className="timer b" > <Timer
                    initialTime={Number(this.state.lasttime1)-Number(this.state.currenttime1)}
                    direction="backward"
                >
                    {() => (
                        <React.Fragment>
                            <table>
                                <tr>
                                    <td>Day</td>
                                    <td>hour</td>
                                    <td>Minute</td>
                                    <td>Second</td>
                                </tr>
                                <tr>
                                    <td><Timer.Days /></td>
                                    <td><Timer.Hours /></td>
                                    <td><Timer.Minutes /> </td>
                                    <td><Timer.Seconds /></td>
                                </tr>
                            </table>
                           
                         
                        </React.Fragment>
                    )}
                </Timer></p>)
                     
                }
                </div>
                </div>
               </div> 
               
               <div className="ranking2">
               {this.state.solved==null && this.userinfo()}
               <button onClick={this.getinfo} className="butoon2">Click to see activity</button>
               <div className="ranklist2"><p className="a"><u>Recent Activity</u></p></div>
               {this.state.object!=null &&
               <table className="qq">
               <tbody>
                   
                  <tr>
                    <th>problemCode</th>
                    <th>Result</th>
                  </tr>
               {this.state.object.map((item,i)=>
                       <tr key={i}>
                           <td >{item.problemCode}</td>
                            <td>{item.result}</td>
                           
                       </tr>
                      
                   )}
               </tbody>
               
               </table> }
              
                </div>
                
      
         </div>
         </div>


    );
  }
}
export default Problem;