  import React, { Component } from 'react';
  import {Route} from 'react-router-dom';
  import axios from 'axios'
  import Callback from './Callback';
  import { Redirect } from 'react-router-dom'
  import {
      BrowserRouter as Router,
      Switch,
      Link
    } from "react-router-dom";

  import Cookies from 'universal-cookie';
  import Search from 'react-search'
  import './Contest.css'
  import Timer from 'react-compound-timer';
  import {refresh_token} from './Api'
 
 class Contest extends React.Component
  {
    constructor(props)
  {
  super(props);
  const cookies = new Cookies();
  const list=[],codelist=[],finallist=[],problemlist=[];
  this.state = {
  list,
  codelist,
  problemlist,
  finallist,
  token:"ht",
  taxt:'',
  clicked:"A",
  suggestions:[],
  show:true,
  var2:null,  



  }
  this.getDataAxios=this.getDataAxios.bind(this);
  this.renderSuggestion=this.renderSuggestion.bind(this);
  this.nextpage=this.nextpage.bind(this);
  this.logout=this.logout.bind(this);
  }













  onTextchanged = (e) => {
  let list=this.state.list;
    let codelisto=(list.map((item)=>item.code.toString()));
    let finallisto=(list.map((item)=>item.name.toString()));
    let codelisto2=codelisto.concat(finallisto);

    {this.setState(() => ({codelist:codelisto2}))};
    console.log(this.state.codelist);

      const value = e.target.value;
      console.log(value.length);
      let suggestions:[];
      if (value.length > 0)
      {
        this.setState({show:true});
      const regex= new RegExp(`${value}`,'i');
      let item=codelisto2;
        const suggestions = item.sort().filter(v => regex.test(v));
        this.setState(()=> ({suggestions:suggestions,taxt:value}));
        console.log(this.state.taxt)
      }
      else
      {
        this.setState(() => ({taxt:value}));
      }
      
      
      

      
  }
  renderSuggestion= () =>
  {
    const suggestions = this.state.suggestions;
    console.log(suggestions.length);

    if(suggestions.length > 0)
    {return (<div><ul >
        {this.state.suggestions.map((suggestions,index) => <li  key={index}>{suggestions}</li>)}</ul></div>
      );}
  }
  getDataAxios=() => {
    const cookies = new Cookies();

    var token1=cookies.get('username');
    console.log('Bearer ' + token1);
          axios.get(`https://api.codechef.com/contests`,
          { headers: {'Content-Type': 'application/json',
                'Accept': 'application/json',
              'Authorization': 'Bearer '+ token1 } }
        )
      .then(res => {this.setState({list:res.data.result.data.content.contestList})})
      .catch(function (error)
      {
        console.log(error.message);
      });
      let list=this.state.list;
      console.log(list);
    
  }

  suggestionSelected= (value) => {
    
    this.setState(() => ({
      taxt:this.state.var2[value],
      suggestions:[],
    }))
    console.log(this.state.var2[value])
    console.log(value)
  }
  logout = (e)=>
  {
    e.preventDefault();
    console.log(this.state.clicked)
    const cookies=new Cookies();
    cookies.remove('username');
    cookies.remove('refresh_token');
    this.setState({clicked:"logout"})
    
  }
  mapping =()=>
  {
    const var1={};
    console.log(this.state.var2)
    {this.state.list.map((item,index) => (var1[item.name]=item.code))}
    {this.state.list.map((item,index) => (var1[item.code]=item.code))}
    this.setState({var2:var1,show:false});
    console.log(var1);
      
  }


  nextpage= () => {
    var value=this.state.taxt;
    const cookies = new Cookies();

  
  

    cookies.set('contestcode',value, {path: "/"});
    this.setState({clicked:"AA"});
  }


    render()
    {
    
  
    const suggestions = this.state.suggestions;
    console.log(suggestions.length);
    const text=this.state.taxt;
    const cookies = new Cookies();
    const contestcode='/frontend/contest/problem/';
    console.log(contestcode);
    let problemlisto=(this.state.problemlist.map((item)=>item.problemCode.toString()));
    console.log(this.state.clicked);
      //css-start
      refresh_token();
    
    




    //css-over
      if(this.state.clicked=="logout")
      {
        return <Redirect to="/frontend/ranking" />
      }
      if(this.state.clicked=="AA")
      {
        return <Redirect to={contestcode} />
      }
      return (
        <div >
          <button onClick={this.logout} className="butoon3" >Logout</button>
          {this.state.list.length===0 &&this.getDataAxios()}
         
          {this.state.show && this.state.list.length>0 && this.mapping()}
	<div className="limiter">
		<div className="container-login100">
			<div className="wrap-login100">
      <div class="login100-form-title pp" >
					<span class="login100-form-title-1" >
						SELECT CONTEST
					</span>
				</div>
      <form className="login100-form ">
					<div className="wrap-input100 validate-input m-b-26" data-validate="Username is required">
						<span className="label-input100">Contest</span>
					
          <input type="text" className="input100" value={text} onChange={this.onTextchanged}  placeholder="Enter contestname"/>
          <ul>
        {this.state.suggestions.map((item,index) => <div className="lii" key={index} onClick={() => this.suggestionSelected(item)}>{item}</div>)}</ul>
        <span class="focus-input100"></span>
          </div>
         
          <div class="container-login100-form-btn">
						<button class="login100-form-btn" onClick={this.nextpage}>
							submit
						</button>
					</div>
          </form>
          
          </div></div></div>
          
        
        <ul>
        {this.state.suggestions.map((item,index) => <div className="lii" key={index} onClick={() => this.suggestionSelected(item)}>{item}</div>)}</ul>
       
        
        
      </div>

        );
    }
  }
  export default Contest;