export function refresh_token(){
  const cookies = new Cookies();
  var refresh_token=cookies.get("refresh_token");
  if(cookies.get('username')!="") return;
  let data={"grant_type":"refresh_token" ,
            "refresh_token":refresh_token, 
            "client_id":"47debda654e625f0c1b7857677c46f3a",
            "client_secret":"feb3726cf8c1934b61d29c23dcb2283b"}
        axios.post(`https://api.codechef.com/oauth/token`,
                    data,
                    {headers:{'Content-Type': 'application/json','Accept': 'application/json'}
        })
      .then(res => {console.log(res);
        cookies.set('username',res.data.result.data["access_token"],res.data.result.data["expires_in"]);
        cookies.set('refresh_token',res.data.result.data["refresh_token"],86400000000);
      })
      .catch(function (error) {
        console.log(error.message);
      });

}



