import React from 'react';
import ReactDOM from 'react-dom';

import './index.css';

import Api from './Api';
import Login from './Login';
import * as serviceWorker from './serviceWorker';
import { CookiesProvider } from 'react-cookie';
import {BrowserRouter,Route,Redirect,Switch} from 'react-router-dom';
import Contest from './Contest'
import Problem from './Problem'
import Problemdetails from './Problemdetails'
import Ranking from './Ranking'
import Submit from './Submit'

let initialStore = { 
    token:"",
};


ReactDOM.render(
 <CookiesProvider>
  <BrowserRouter>
   
    
    <Switch>
    	<Route path="/frontend/" exact component={Api} />
    	<Route path='/frontend/contest' exact component={Contest}></Route> 
		<Route path='/frontend/contest/ranking/' exact component={Ranking}></Route> 
	
		<Route path="/frontend/contest/problem" exact component={Problem}></Route>
		<Route path="/frontend/contest/problem/:id/:id1" exact component={Submit}></Route>
		<Route path="/frontend/contest/problem/:id"  exact component={Problemdetails}></Route>
		
   	</Switch>
   	</BrowserRouter>
 </CookiesProvider>
, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();