import React, { Component } from 'react';
import {Route} from 'react-router-dom';
import axios from 'axios'

import { Redirect } from 'react-router-dom'
import {
    BrowserRouter as Router,
    Switch,
    Link
  } from "react-router-dom";

import Cookies from 'universal-cookie';

import {Dropdown} from 'react-dropdown';
import Timer from 'react-compound-timer';
import './Submit.css'
import {refresh_token} from './Api'
class Submit extends React.Component
{
 

    constructor(props)
    {
    super(props);
    const cookies = new Cookies();
    const list=[],codelist=[],finallist=[],problemlist=[];
    
    
    this.state = {
     
        object:null,
        languages:[],
        languageselected:'',
     token:"ht",
    taxt:cookies.get('problemcode1'),
    clicked:"A",
    value: '',
        language:'C++ 6.3',
        code:'',
        input:'',
        output:'',
        link:'',
        error:'',
    
    
    }
    this.showproblem=this.showproblem.bind(this);
    
    
    }
    showproblem= () => {
        const cookies = new Cookies();

  var token1=cookies.get('username');
  var value1=cookies.get('contestcode');
  var value=cookies.get('problemcode1');
  let object=null;
  axios.get(`https://api.codechef.com/contests/${value1}/problems/${value}?fields=`,
  { headers: {'Content-Type': 'application/json',
        'Accept': 'application/json',
      'Authorization': 'Bearer '+ token1 } }
     ) .then(res => {this.setState({object:res.data.result.data.content,languages:res.data.result.data.content.languagesSupported})})
.catch(function (error)
{
  console.log(error.message);
});
let problemdetailso=this.state.problemdetails;
  

    
    
    }
    logout = (e)=>
    {
     
      console.log(this.state.clicked)
      const cookies=new Cookies();
      cookies.remove('username');
      cookies.remove('refresh_token');
      this.setState({clicked:"logout"})
      
    }
    
    submit =() =>
    {
        const cookies = new Cookies();

        var token1=cookies.get('username');
        var value1=cookies.get('contestcode');
        var value=cookies.get('problemcode1');
        
        let code = this.state.code.toString();
        let language=this.state.language.toString();
        let input=this.state.input.toString();
        
        let data={"sourceCode":this.state.code.toString(),
        "language":this.state.language,
        "input":this.state.input.toString()}
        console.log(token1)
        axios.post(`https://api.codechef.com/ide/run`,data,
        { headers: {'Content-Type': 'application/json',
              'Accept': 'application/json',
            'Authorization': 'Bearer '+ token1 } }
           ) .then(res => {this.setState({link:res.data.result.data.link})})
      .catch(function (error)
      {
        console.log(error.message);
      });
      console.log(this.state.link)
      let problemdetailso=this.state.problemdetails;
      if(this.state.link!='')
     {
         let link=this.state.link.toString();
         console.log(link);
         let data1={"link":{link}}
        axios.get(`https://api.codechef.com/ide/status?link=${link}`,
        { headers: {'Content-Type': 'application/json',
              'Accept': 'application/json',
            'Authorization': 'Bearer '+ token1 } }
           ) .then(res => {this.setState({output:res.data.result.data.output})})
      .catch(function (error)
      {
        console.log(error.message);
      });
      console.log(this.state.output);
      
     }
        
    
      
        
    }
    
   handleChange =(e)=>
   {
       e.persist();
        const language=e.target.value;
        this.setState({language:language});
        console.log(language)
   }
   changecode =(e)=>
   {
       const code=e.target.value;
       this.setState({code:code});
   }
   changeinput =(e)=>
   {
       const input=e.target.value;
       this.setState({input:input});
   }


  


  render()
  {
    refresh_token();
    if(this.state.clicked=="logout")
    {
      return <Redirect to="/frontend/" />
    }
    if(this.state.object != null)
    {
        const data=this.state.languages["0"];
        console.log(data)
        const defaultOption='ram';  
        return (
        <div>
          <button onClick={this.logout} className="butoon3" >Logout</button>
          <div className="qq">
           
           
            <table>
              <tr>
                <td>Language</td>
                <td>
            <select onChange={this.handleChange}>
              {this.state.languages.map(languages => <option key={languages}  value={languages}>{languages}</option>)}
            </select>
            </td>

            </tr>
            

            <tr>
              <td><h5>Source code:</h5></td>
            
            
            <td><textarea rows="20" cols="70" value={this.state.code} onChange={this.changecode} /></td>
            
            </tr>
           
            <tr>
            <td><h5>Input</h5><br/></td>
            <td> <textarea rows="5" cols="50" value={this.state.input} onChange={this.changeinput} /></td>
            
            </tr>
            <tr>
            <td> <h5>Output</h5></td>
           <td> <textarea rows="5" cols="50" value={this.state.output} /><br/></td>
            
          
            </tr>
            </table>
            <td><button onClick={this.submit}>submit</button></td>
            </div>
            </div>
        );
    }
 
  
    return (
        <div>
         {this.state.object===null && this.showproblem()}
        
        </div>
        
        );
  }
}
export default Submit;