import React from 'react'
import axios from 'axios'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";

class Login extends React.Component{
    constructor(props){
        super(props);
        this.state={token:"Hello"};
        this.X=this.X.bind(this);
        this.getPermission=this.getPermission.bind(this);
        this.getauthtoken=this.getauthtoken.bind(this);
        this.check=this.check.bind(this);
    }
    X=()=> {
        var vars = {};
        var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
            vars[key] = value;
        });
        return vars;
    }
    getPermission= () => {
        window.location.replace("https://api.codechef.com/oauth/authorize?response_type=code&client_id=47debda654e625f0c1b7857677c46f3a&state=xyz&redirect_uri=http://localhost:3000/");
    }
    getauthtoken=()=>{
        var code=this.X()["code"];
        let data={ "grant_type": "authorization_code",
                    "code":code,
                   "client_id":"47debda654e625f0c1b7857677c46f3a",
                   "client_secret":"feb3726cf8c1934b61d29c23dcb2283b",
                   "redirect_uri":"http://localhost:3000/" 
                }
                console.log(data);
    axios.post(`https://api.codechef.com/oauth/token`,
                    data,
                    {headers:{'Content-Type': 'application/json','Accept': 'application/json'}
        })
      .then(res => {
        this.setState({token:res.data.result.data["access_token"]});
      })
      .catch(function (error) {
        console.log(error.message);
      });
    }
    check= ()=>{
        var code=this.X()["code"];
        if(code!=null)
         this.getauthtoken();
        else
         this.getPermission();
    }
    render(){
        return <div>
            <h1>{this.state.token}</h1>
            <button onClick={this.check}>
                 Login
            </button>
        </div>
    }
}
export default Login;